ed costs. You should not engage in trading unless you fully understand the nature of the transaction you are entering into and the extent of your exposure to loss. If you do not fully understand these risks, you should conduct additional research or seek independent advice from your financial advisor to determine whether such trading suits your needs and risk tolerance. ")]),_c('p',{staticStyle:{"padding-top":"3pt","padding-left":"5pt","text-indent":"0pt","text-align":"justify"}},[_vm._v(" The Site is not intended as, and does not provide, any investment or financial advice. With respect to any financial or investment decisions, we recommend you conduct your own research to properly evaluate the risks and benefits of any investment or financial transaction. We recommend you seek the advice and guidance of qualified accountants, financial advisors, tax advisors, legal counsels and investment advisors, in connection with any investment or financial transaction. ")]),_c('p',{staticStyle:{"text-indent":"0pt","text-align":"left"}},[_c('br')]),_c('p',{staticStyle:{"padding-left":"5pt","text-indent":"0pt","text-align":"justify"}},[_vm._v(" You agree to be held completely and fully responsible for your decisions. We do not guarantee the completeness or accuracy of any information provided to you through the Site and we are not liable for any errors in actions taken in reliance thereon, including with respect to the release of any funds. ")]),_c('p',{staticStyle:{"text-indent":"0pt","text-align":"left"}},[_c('br')]),_c('p',{staticStyle:{"padding-left":"5pt","text-indent":"0pt","text-align":"justify"}},[_vm._v(" Any and all transactions, trades, purchases, sales, and exchanges entered into with cryptocurrency, including any fraudulent or accidental transactions concluded using your cryptocurrency, may be final and may not be reversed. There is a risk that transactions cannot be settled or are delayed at settlement, that processing times differ for each transaction, or transaction may be incorrectly processed. ")]),_c('p',{staticStyle:{"text-indent":"0pt","text-align":"left"}},[_c('br')]),_c('p',{staticStyle:{"padding-left":"5pt","text-indent":"0pt","text-align":"justify"}},[_vm._v(" No central bank, regulator or government backs cryptocurrencies. Digital currencies are not legal tender in the United States. A creation or change in laws, statutes, or regulations, either domestically and/or internationally, may affect your ability to access or use the Site, or your ability to use, purchase, holding or transfer, as well as the value, of any cryptocurrency. Each cryptocurrency market will have varying degrees of liquidity. You may not be able to buy or sell a cryptocurrency at any specific time. ")]),_c('p',{staticStyle:{"text-indent":"0pt","text-align":"left"}},[_c('br')]),_c('p',{staticStyle:{"padding-left":"5pt","text-indent":"0pt","text-align":"justify"}},[_vm._v(" You acknowledge and understand that cryptography is a progressing field. Advances in code cracking or technical advances such as the development of quantum computers may present risks to cryptocurrencies and the Site, which could result in the theft or loss of your cryptocurrency. To the extent possible, we intend to update the protocol underlying the Site to account for any advances in cryptography and to incorporate additional security measures, but we do not guarantee or otherwise represent full security of the system. By using the Site, you acknowledge these inherent risks. ")]),_c('p',{staticStyle:{"text-indent":"0pt","text-align":"left"}},[_c('br')]),_c('p',{staticStyle:{"padding-left":"5pt","text-indent":"0pt","text-align":"justify"}},[_vm._v(" Cryptocurrency may be subject to taxation or levies. It is your sole responsibility to determine whether, and to what extent, any taxes apply to any transactions you conduct using the Site, and to withhold, collect, report and remit the correct amounts of taxes to the appropriate tax authorities "),_c('span',{staticStyle:{"color